$bgcolor: #1d1d1d;
$button-bg: #ffd700;
$colorYel: #ffd700;
$textcolor2: #e3b873;
$textcolor3: #363636;
$textcolor4: #181818;


@mixin flex($hor, $vert, $flow) {
  display: flex;
  justify-content: $hor;
  align-items: $vert;
  flex-flow: $flow;
}

main.landing {
  @include flex(center, center, column);

  section {
    height: auto;
    border: none;
    color: #fff;
  }
  .about-app {
    @include flex(space-around, center, row);
    width:100%;
    margin: 20px 0 0 0;

    .description {
      margin: 40px 0;
    }

    h1 {
      font-size:48px;
    }

    p {
      font-size:28px;
      margin: 35px 0;
    }

    button {
      border: none;
      background: #ffd700;
      font-weight: 600;
      padding: 10px 33px;
      border-radius: 2px;
      cursor: pointer;
      transition: 0.3s;
      margin: 0 8px;
      font-size: 23px;

      &:hover {
        background: #fff;
      }
    }

    img {
      max-width: 650px;
      width: 100%;
    }
  }

  .examples {
    @include flex(space-around, center, row);
    width: 100%;
    margin: 23px 0 0 0;
    padding: 45px 0;
    background: #222222;

    h2 {
      font-size: 30px;
    }

    ul {
      @include flex(space-evenly, center, row);
      width: 20%;

      img {
        width: 100px;
      }
    }
  }

  .features {
    @include flex(space-around, center, row);
    width: 100%;
    margin: 0;
    padding: 45px 0;
    background: #515151;

    h2 {
      font-size: 30px;
    }

    li {
      margin: 8px 0;
    }
  }
}
