@import url('./reset.scss');

$break-laptop: 1390px;
$break-tablet: 1190px;
$break-smart:800px;
$break-phone:640px;

$bgcolor: #1d1d1d;
$button-bg: #ffd700;
$colorYel: #ffd700;
$textcolor2: #e3b873;
$textcolor3: #363636;
$textcolor4: #181818;


@mixin flex($hor, $vert, $flow) {
  display: flex;
  justify-content: $hor;
  align-items: $vert;
  flex-flow: $flow;
}

body {
  background: $bgcolor;
  font-family: Arial, Helvetica, sans-serif;
}


header {
  @include flex(space-between, center, row);
  background: #2d2d2d;

  .logo {
    margin: 0 0 0 16px;

    img {
      width:84px;
      padding: 6px 0;
    }
  }

  .credentials {
    @include flex(flex-end, center, row);
    width: 45%;
    button {
      border: none;
      background: $button-bg;
      font-weight: 600;
      padding: 4px 6px;
      border-radius: 2px;
      cursor: pointer;
      transition: 0.3s;
      margin: 0 8px;
      &:hover{
        background:#fff;
      }
    }
  }
}

main {
  @include flex(center, unset, row);
  overflow: hidden;

  section {
    box-sizing: border-box;
    border:1px solid #fff;
    height: 86vh;
  }
}

.tools {
  width: 9%;

  .pen-size {
    @include flex(space-evenly, unset, row);
    width: 81%;
    margin:10px auto;

    .active {
      border-color: #ffd700;
      &:before {
        background: #ffd700;
      }
    }

    span {
      display: block;
      width:20px;
      height:20px;
      border:2px solid #444;
      cursor: pointer;
      &:before{
        content: '';
        display: block;
        background: #fff;
      }
    }

    .size-1x::before {
      content:'';
      width: 8px;
      height: 8px;
      margin: 30%;
    }

    .size-2x::before {
      content:'';
      width: 10px;
      height: 10px;
      margin: 25%;
    }

    .size-3x::before {
      content:'';
      width: 12px;
      height: 12px;
      margin: 20%;
    }

    .size-4x::before {
      content:'';
      width: 16px;
      height: 16px;
      margin: 10%;
    }

  }

  .instruments {
    @include flex(unset, center, row);
    flex-wrap: wrap;
    width: 76%;
    margin: 0 auto;

    .tool {
      width: 42px;
      height: 42px;
      border: 2px solid #3a3a3a;
      margin: 2px;
      cursor: pointer;
      position: relative;
      &.active {
        border: 2px solid $colorYel;
      }
      &.pen {
        background: #3a3a3a url(../assets/img/pen.png);
        background-size: 65%;
        background-position: 50%;
        background-repeat: no-repeat;
      }
      &.color-pick {
        background: #3a3a3a url(../assets/img/picker.png);
        background-size: 65%;
        background-position: 50%;
        background-repeat: no-repeat;
      }

      &.bucket {
        background: #3a3a3a url(../assets/img/bucket.png);
        background-size: 65%;
        background-position: 50%;
        background-repeat: no-repeat;
      }

      &.erase {
        background: #3a3a3a url(../assets/img/eraser.png);
        background-size: 65%;
        background-position: 50%;
        background-repeat: no-repeat;
      }
      &.stroke {
        background: #3a3a3a url(../assets/img/stroke.png);
        background-size: 65%;
        background-position: 50%;
        background-repeat: no-repeat;
      }
      &.rect {
        background: #3a3a3a url(../assets/img/rect.png);
        background-size: 65%;
        background-position: 50%;
        background-repeat: no-repeat;
      }
    }

  }

  .fast-colors {
    position: relative;
    margin: 26px 26px;
    width: 100%;
    min-height:70px;


    li {
      width: 40px;
      height: 40px;
      background: #fff;
      position: absolute;
      z-index: 100;
      border: 5px solid #888888;
      cursor: pointer;

      &.secondary{
        background: #c71919;
        top: 32px;
        z-index: 99;
        left: 32px;
      }
    }

  }

  form {
    @include flex(center, center, row);
    label {
      color: $colorYel;
      margin: 0 10px;
    }
    input {
      height: 20px;
      width: 20px;
      border: none;
      -webkit-appearance: none;
      cursor:pointer;
    }
    input::-webkit-color-swatch-wrapper {
      background: none;
      padding: 0;
    }
    input::-webkit-color-swatch {
      background: none;
      border: none;
    }
  }
}


.frames {
  width: 10%;
  overflow-y: scroll;
  ul {
    @include flex(unset, center, column);

    .frame{
      position: relative;
      width: 98px;
      height: 98px;
      background: url(../assets/img/dotted-back.png);
      border: 4px solid #444444;
      margin: 15px 0 0 0;
      cursor: pointer;
      border-radius: 3px;

      img {
        position: absolute;
        width: 98px;
        height: 98px;

        &.hidden{
          visibility: hidden;
        }
      }
      &:hover {
        border: 4px solid #999999;
      }
      &.active {
        border: 4px solid #ffd700;
      }
      &.empty {
        background: none;
        border: 4px dashed #ffd700;
      }
      &.leave {
        z-index: 1000;
        position: absolute;
      }
    }
  }

  .controls {
    position: relative;
    width: 98px;
    height: 98px;

    .num {
      display: block;
      width: 16px;
      height: 16px;
      background: #ffd700;
      padding: 5px;
      text-align: center;
      position: absolute;
      top:0;
      left:0;
    }

    .del {
      position: absolute;
      top: 0;
      right: 0;
      &:hover {
        &:before {
          background: rgba(58,58,58, 0.85);
          width: 15px;
          height: 15px;
        }
      }

      &:before {
        content: url(../assets/img/trash.svg);
        display: block;
        width: 16px;
        height: 16px;
        background: rgba(58,58,58, 0.4);
        padding: 4px 5px 7px 6px;
      }
    }

    .move {
      position: absolute;
      bottom: 0;
      left: 0;
      &:hover {
        &:before {
          background: rgba(58,58,58, 0.85);
          width: 15px;
          height: 15px;
        }
      }

      &:before {
        content: url(../assets/img/arrows.svg);
        display: block;
        width: 16px;
        height: 16px;
        background:rgba(58,58,58, 0.4);
        padding: 4px 5px 7px 6px;
      }
    }

    .copy {
      position: absolute;
      bottom: 0;
      right: 0;
      &:hover {
        &:before {
          background: rgba(58,58,58, 0.85);
          width: 15px;
          height: 15px;
        }
      }

      &:before {
        content: url(../assets/img/copy.svg);
        display: block;
        width: 16px;
        height: 16px;
        background:rgba(58,58,58, 0.4);
        padding: 4px 5px 7px 6px;
      }
    }
  }

  .add-new-frame {
    width: 100px;
    padding: 4px 5px 4px 30px;
    text-align: left;
    margin: 15px auto 0 auto;
    display: flex;
    background: none;
    color: #ddd;
    border: 2px solid #ddd;
    border-radius: 3px;
    cursor:pointer;
    &:hover{
      border: 2px solid #ffd700;
    }
    &::before {
      content: url(../assets/img/plus.svg);
      display: block;
      width: 30px;
      margin: 2px 8px 0 -22px;
    }
  }
}

.draw {
  width: 61%;
  @include flex(center, center, column);
  overflow: hidden;
  .draw-container {
    width: 512px;
    height: 512px;
    position: relative;
  }
  #drawCanvas {
    position: absolute;
    z-index: 100;
    background: url(../assets/img/dotted-back.png);
  }

  #drawCanvasAlt {
    background: transparent;
    position: absolute;
    z-index: 90;
  }

  .options {
    @include flex(space-evenly, center, row);
    width: 80%;
    margin:20px 0;
    color: #FFD700;
    font-weight: 600;

    .coord span {
      display: inline-block;
      width: 25px;
    }

  }
}

.preview {
  @include flex(flex-start, center, column);
  padding:10px 0;
  width: 19%;
  .options {
    @include flex(space-around, unset, row);
    width: 35%;
    button {
      color: #000000;
      background: $button-bg;
      border: none;
      font-size: 12px;
      font-weight: 600;
      padding: 3px;
      border-radius: 1px;
      cursor: pointer;
      &:hover{
        color: #ffffff;
        text-shadow: 0px 0px 2px #000;
      }
    }
  }

  .prev-frame {
    @include flex(center, flex-end, row);
    width: 238px;
    height: 238px;
    background: url(../assets/img/dotted-back.png);
    margin: 10px auto 20px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .fps-set {
    @include flex(space-evenly, center, row);
    width: 70%;
    margin: 0 auto;
    label {
      color: #fff;
      font-weight: 600;
    }
    input{
      -webkit-appearance: none;
      width: 50%;
      height: 4px;
      background: #d3d3d3;
      outline: none;
      opacity: 0.9;
      -webkit-transition: .2s;
      transition: opacity .2s;
      &:hover{
        opacity: 1;
      }
    }
    input::-webkit-slider-thumb{
      -webkit-appearance: none;
      appearance: none;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background:$button-bg;
      cursor: pointer;
    }
    input::-moz-range-thumb {
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background:$button-bg;
      cursor: pointer;
    }
  }

  .palette-manager {
    @include flex(unset, unset, row);
    width: 80%;
    margin: 20px auto;
    border: 3px solid #888888;
    border-radius: 5px;
    flex-wrap: wrap;

    p {
      color: #fff;
      font-size: 20px;
      font-weight: bold;
      width: 80%;
      padding: 5px 10px;
    }

    button {
      font-size: 28px;
      height: 34px;
      font-weight: 600;
      background: none;
      color: #fff;
      border: none;
      cursor: pointer;
    }

    ul {
      @include flex(unset, unset, row);
      flex-wrap: wrap;
      width: 100%;
      list-style: none;
      border-top: 2px solid #999;
      padding: 10px;

      li {
        width: 16px;
        height: 16px;
        border: 1px solid #888;
        margin: 2px;
      }
    }
  }

}

footer {
  @include flex(space-evenly, center, row);
  padding: 30px;
  p {
    color: $colorYel;
    font-size: 14px;
    font-weight: 600;
  }

  a{
    text-decoration: underline;
    &:hover {
      color: $colorYel;
    }
  }

}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  z-index: 500;

  .modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffd700;
    padding: 10px;
    width: 24rem;
    border-radius: 0.3rem;

    .close-button {
      float: right;
      width: 1.5rem;
      line-height: 1.5rem;
      text-align: center;
      cursor: pointer;
      font-size: 2.0rem;
    }

    .modal-content h3 {
      margin: 0 0 15px 0;
    }
  }

  .palette-container {
    @include flex(unset, unset, column);
    background: #444444;
    color: #fff;

    .palette-templates {
      width: 95%;
      @include flex(flex-start, unset, row);
      flex-wrap: wrap;
      padding: 10px 10px;
      height: 150px;
      overflow-y: scroll;
      background: #444444;
      border-bottom: 5px dashed $colorYel;

      span {
        position: absolute;
        top: 0;
        right: 5px;
        cursor: pointer;
      }

      li.template {
        width: 44px;
        height: 44px;
        border: 2px solid $colorYel;
        margin: 0 4px;
        position: relative;
      }

      li.add {
        @include flex(center, center, row);
        border: 2px dashed $colorYel;
        color: $colorYel;
        cursor: pointer;
      }
    }

    .picker {
      @include flex(space-evenly, unset, row);
      width: 100%;
      padding: 10px 0 10px 0;
      background: #000000;

      #palettCnv {
        cursor: crosshair;
      }
    }

    .color-info {
      @include flex(space-evenly, center, row);
      border-top: 5px dashed $colorYel;
      padding: 7px;

      .current-color {
        width: 18px;
        height: 18px;
        border: 1px solid $colorYel;
      }

      .hex{
        width: 29%;
      }

      li.rgb {
        width: 42%;
      }
    }
  }
}
